<template>
  <chart-card :title="title" class="chart-card">
    <template>
      <!-- <v-chart :ref="refName" :height="100" :options="options" theme="macarons" :autoresize="true" /> -->
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 100%;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getChartApi } from '@/apis/modules/analysis/orderContract'

export default {
  name: 'RatioCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  props: {
    title: {
      type: String,
      default: '合同到货分析'
    }
  },
  data() {
    return {
      refName: 'Ratio',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getChartApi({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.handleData(data)
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleData(percentageChart) {
      const { data, xAxisData } = percentageChart
      const xAxis = [
        {
          data: xAxisData,
          // axisLabel: {
          //   interval: 0
          // },
          bottom: 200,
          axisTick: {
            length: 480 // 延长坐标轴刻度的长度，用作表格竖线
          },
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (params) => {
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 4 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            }
          }
        }
      ]
      const legendData = []
      let bottom = 20
      const series = data.map((item, index) => {
        legendData.push(item.name)
        xAxis.push({
          data: item.data,
          position: 'bottom', // 将坐标轴定位到底部
          offset: 20 + index * 20, // 设置位置偏移
          axisTick: {
            show: false
          }
        })
        bottom += 20
        return {
          name: item.name,
          data: item.bar,
          stack: 'ad',
          type: 'bar'
        }
      })

      xAxis.push({// 最后加一个无data的项，作为表格最底部的线
        position: 'bottom',
        offset: bottom,
        axisTick: {
          show: false
        }
      })

      this.getCompleteOptions(legendData, series, xAxis, bottom + 1)

      // this.loading = true

      // setTimeout(() => {
      //   const data = [
      //     {
      //       name: '未到货金额',
      //       type: 'bar',
      //       stack: 'Ad',
      //       bar: [23.2, 25.6, 76.7, 135.6, 162.2, 30],
      //       data: [1, 1, 1, 1, 1, 1]
      //     },
      //     {
      //       name: '到货金额',
      //       type: 'bar',
      //       stack: 'Ad',
      //       bar: [76.7, 6.4, 2, 4.9, 7.0, 8],
      //       data: [2, 2, 2, 2, 2, 2]
      //     }
      //   ]
      //   const xAxisData = ['C001', 'C002', 'C003', 'C004', 'C005', 'C006']
      //   const xAxis = [
      //     {
      //       data: xAxisData,
      //       // axisLabel: {
      //       //   interval: 0
      //       // },
      //       bottom: 200,
      //       axisTick: {
      //         length: 480 // 延长坐标轴刻度的长度，用作表格竖线
      //       },
      //       axisLabel: {
      //         show: true,
      //         interval: 0, // 使x轴文字显示全
      //         formatter: (params) => {
      //           var newParamsName = ''
      //           var paramsNameNumber = params.length
      //           var provideNumber = 4 // 一行显示几个字
      //           var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
      //           if (paramsNameNumber > provideNumber) {
      //             for (var p = 0; p < rowNumber; p++) {
      //               var tempStr = ''
      //               var start = p * provideNumber
      //               var end = start + provideNumber
      //               if (p === rowNumber - 1) {
      //                 tempStr = params.substring(start, paramsNameNumber)
      //               } else {
      //                 tempStr = params.substring(start, end) + '\n'
      //               }
      //               newParamsName += tempStr
      //             }
      //           } else {
      //             newParamsName = params
      //           }
      //           return newParamsName
      //         }
      //       }
      //     }
      //   ]
      //   const legendData = []
      //   const series = data.map((item, index) => {
      //     legendData.push(item.name)
      //     xAxis.push({
      //       data: item.data,
      //       position: 'bottom', // 将坐标轴定位到底部
      //       offset: 20 + index * 20, // 设置位置偏移
      //       axisTick: {
      //         show: false
      //       }
      //     })
      //     return {
      //       name: item.name,
      //       data: item.bar,
      //       stack: 'ad',
      //       type: 'bar'
      //     }
      //   })

      //   xAxis.push({// 最后加一个无data的项，作为表格最底部的线
      //     position: 'bottom',
      //     offset: 80,
      //     axisTick: {
      //       show: false
      //     }
      //   })

      //   this.getCompleteOptions(legendData, series, xAxis)
      //   // charts.hideLoading()
      // }, 1000)
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis, bottom) {
      this.options = {
        grid: {
          bottom, // 调整图表容器距离底部的距离，给表格腾空间
          left: 120, // table 距离右侧的宽度
          top: 10,
          right: 10
        },
        legend: { // 图例调整位置，对应表格的相应行
          data: legendData,
          bottom: 0,
          left: 0,
          orient: 'vertical',
          itemGap: 5
        },
        xAxis,
        // xAxis: [
        //   {
        //     data: xAxis,
        //     axisLabel: {
        //       interval: 0
        //     },
        //     axisTick: {
        //       length: 80 // 延长坐标轴刻度的长度，用作表格竖线
        //     }
        //   },
        //   {
        //     data: [1, 1, 1, 1, 1],
        //     position: 'bottom', // 将坐标轴定位到底部
        //     offset: 20, // 设置位置偏移
        //     axisTick: {
        //       show: false
        //     }
        //   },
        //   {
        //     data: [2, 2, 2, 2, 2],
        //     position: 'bottom',
        //     offset: 40,
        //     axisTick: {
        //       show: false
        //     }
        //   },
        //   {
        //     data: [3, 3, 3, 3, 3],
        //     position: 'bottom',
        //     offset: 60,
        //     axisTick: {
        //       show: false
        //     }
        //   },
        //   {// 最后加一个无data的项，作为表格最底部的线
        //     position: 'bottom',
        //     offset: 80,
        //     axisTick: {
        //       show: false
        //     }
        //   }
        // ],
        yAxis: {},
        series
        // series: [
        //   {
        //     name: '期初库存',
        //     type: 'bar',
        //     data: [1, 1, 1, 1, 1]
        //   },
        //   {
        //     name: '期末库存',
        //     type: 'bar',
        //     data: [2, 2, 2, 2, 2]
        //   },
        //   {
        //     name: '出库',
        //     type: 'bar',
        //     data: [3, 3, 3, 3, 3]
        //   }
        // ]
      }

      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 10px);
  }
}
</style>
