<template>
  <chart-card :title="title" class="chart-card">
    <template>
      <!-- <v-chart :ref="refName" :height="100" :options="options" theme="macarons" :autoresize="true" /> -->
      <!-- <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleUpdateCharts"
      >更新</el-button>
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="saveImage"
      >导出</el-button> -->
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 100%;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import moment from 'moment'
moment.locale('zh-cn')
import { uploadApi } from '@/apis/modules/common'
import { getChart5and6Api } from '@/apis/modules/analysis/inventory'

export default {
  name: 'InventoryAccountCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  props: {
    title: {
      type: String,
      default: '库存账龄图'
    }
  },
  data() {
    return {
      refName: 'InventoryAccount',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getChart5and6Api({
          startDate: moment(new Date()).subtract(1, 'months').format('YYYY-MM-DD'),
          endDate: moment(new Date()).format('YYYY-MM-DD')
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            // amountChart 库存账龄图  percentageChart 库存账龄占比
            // eslint-disable-next-line no-unused-vars
            const { amountChart, percentageChart } = data
            const { series, xAxis } = amountChart
            const legendData = series.map(i => i.name)
            this.getCompleteOptions(legendData, series, xAxis)
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
    },
    handleUpdateCharts() {
      setTimeout(() => {
        const legendData = ['库存', '入库', '出库', '增减']
        const series = [
          {
            name: '库存',
            type: 'bar',
            data: [12, 12.0, 14.9, 17.0, 3.2, 125.6, 56.7, 35.6, 62.2, 132.6, 120.0, 16.4]
          },
          {
            name: '入库',
            type: 'bar',
            data: [96.7, 35.6, 62.2, 132.6, 120.0, 16.4, 12, 12.0, 41.9, 71.0, 223.2, 215.6]
          },
          {
            name: '出库',
            type: 'bar',
            data: [86.7, 635.6, 1162.2, 32.6, 20.0, 6.4, 2, 2.0, 4.9, 7.0, 23.2, 25.6]
          },
          {
            name: '增减',
            type: 'bar',
            data: [3, 5, 6, 70, 8, 9, 0, 0, 5, 32, 50, 55]
          }
        ]
        const xAxis = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
        this.getCompleteOptions(legendData, series, xAxis)
      }, 1000)
    },
    saveImage() {
      this.$nextTick(() => {
        // 获取的是echarts的base64的图片
        const chart = this.chart.getDataURL({
          type: 'base64', // 导出的格式，可选 png, jpeg
          pixelRatio: 1 // 导出的图片分辨率比例，默认为 1。
        })

        // 这里对base64串进行操作，去掉url头，并转换为byte
        var bytes = window.atob(chart.split(',')[1])

        var array = []
        for (var i = 0; i < bytes.length; i++) {
          array.push(bytes.charCodeAt(i))
        }
        var blob = new Blob([new Uint8Array(array)], { type: 'image/jpeg' })
        var formData = new FormData()
        formData.append('file', blob, Date.now() + '.jpg')

        // http://8.130.16.201:18990/file/pic/1694010979965-20230906103620136.jpg
        // http://8.130.16.201:18990/file/pic/1694050186595-20230907092947382.jpg

        uploadApi(
          '/sys/file/upload',
          formData
        ).then(res => {
          console.log(res)
          const { code, data } = res
          if (!code) {
            console.log('图片的地址：', `http://8.130.16.201:18990${data.fileUrl}`)
          }
        }).catch(err => {
          console.log(err)
        })
      })
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
</style>
