<template>
  <chart-card :title="title" class="chart-card">
    <template>
      <!-- <v-chart :ref="refName" :height="100" :options="options" theme="macarons" :autoresize="true" /> -->
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 100%;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getChart4Api } from '@/apis/modules/analysis/inventory'

export default {
  name: 'InventoryTransferRatioCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  props: {
    title: {
      type: String,
      default: '库存周转率'
    }
  },
  data() {
    return {
      refName: 'InventoryTransferRatio',
      options: {},
      nameOpts: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getChart4Api({
          ...this.$attrs.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            const { series, fullName, xAxis } = data
            this.nameOpts = fullName.reduce((total, cur, index) => {
              total[cur] = xAxis[index]
              return total
            }, {})
            const legendData = series.map(i => i.name)
            this.getCompleteOptions(legendData, series, fullName)
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
          // formatter: function(params) {
          //   console.log('@@@@------@@@@@@', params)
          //   let s = ''
          //   params.forEach(item => {
          //     s += item.name + '</br>' + item.marker + item.seriesName + ':' + item.value
          //   })
          //   return s
          // }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (label) => {
              const params = this.nameOpts[label] || ''
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 2 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            }
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
</style>
