// Tip 订单合同统计分析
import request from '@/apis/request.wms'

// 主列表
export function getListApi(data) {
  return request({
    url: '/statistics/wmsContractAnalysis/pageList',
    method: 'post',
    data
  })
}

// 详情列表
export function getDetailListApi(data) {
  return request({
    url: '/statistics/wmsContractAnalysis/detail',
    method: 'post',
    data
  })
}

// 统计图
export function getChartApi(data) {
  return request({
    url: '/statistics/wmsContractAnalysis/chart',
    method: 'post',
    data
  })
}

// 自定义列表刷新图表
export function wmsInventoryAnalysisRefreshApi(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/refresh',
    method: 'post',
    data
  })
}

// 实时分析库存自定义导出
export function exportInventoryAnalysisApi(data) {
  return request({
    url: '/statistics/excel/export/inventoryAnalysisByCustom',
    method: 'post',
    responseType: 'blob',
    timeout: 500000,
    data
  })
}
