// Tip 供应商综合分析
import request from '@/apis/request.wms'

// 主列表
export function getListApi(data = {}) {
  return request({
    url: '/statistics/wmsSupplierComprehensiveAnalysis/pageList',
    method: 'post',
    data
  })
}

// getChart1Api
export function getChart1Api(data = {}) {
  return request({
    url: '/statistics/wmsSupplierComprehensiveAnalysis/chartData1',
    method: 'post',
    data
  })
}

