// Tip 实时库存分析
import request from '@/apis/request.wms'

// 主列表
export function getListApi(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/pageList',
    method: 'post',
    data
  })
}

// 详情列表
export function getDetailListApi(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/detailList',
    method: 'post',
    data
  })
}

// 库存占比图
export function getChart1Api(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/inventoryPieChart',
    method: 'post',
    data
  })
}

// 账龄占比
export function getChart2Api(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/agePieChart',
    method: 'post',
    data
  })
}

// 月度库存
export function getChart3Api(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/monthlyColumnChart',
    method: 'post',
    data
  })
}

// 月度库存
export function getChart4Api(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/inventoryTurnoverColumnChart',
    method: 'post',
    data
  })
}

// 库存账龄&占比柱状图
export function getChart5and6Api(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/inventoryAgeColumnChart',
    method: 'post',
    data
  })
}

// 实时分析库存自定义导出
export function exportInventoryAnalysisApi(data) {
  return request({
    url: '/statistics/excel/export/inventoryAnalysisByCustom',
    method: 'post',
    responseType: 'blob',
    timeout: 500000,
    data
  })
}

// 自定义列表刷新图表
export function wmsInventoryAnalysisRefreshApi(data) {
  return request({
    url: '/statistics/wmsInventoryAnalysis/refresh',
    method: 'post',
    data
  })
}
